import React, { Component } from 'react'

export default class Experience extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-experience" data-section="experience">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                <h2 className="colorlib-heading animate-box">Work Experience</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-3">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                      <h2> Aug 2019 - Nov 2019 </h2>

                        <h2>Software Engineer Intern at iConsult Colloborative , New York </h2>
                        <ul>
                        <li>Redesigned and maintained existing official website used by 500+ students and 100+ faculty to improve user experience using
                          modern technologies and web frameworks like Angular JS, React.js, Node.js, HTML, CSS and JavaScript.</li> 
                          <li>Improved efficiency of operating by over 30% by developing a brand new admin panel for professors 
                              and teaching assistant.</li>
                          <li>Designed test cases web application as well as debugged and diagnosed the user issues 
                              encountered in system.</li>
                          <li>Managed code versioning with GitLab/GIT and worked in an Agile driven environment.</li>
                          </ul>
                          </div>
                    
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInTop">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-2">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                      <h2> Aug 2016-July 2018 </h2>

                        <h2>Software Engineer at Tata Consultancy Services,India</h2>
                        <ul>
                        <li>Involved in route to live process-design, coding, logging, unit testing, integration testing and system testing, live deployment and
                          post implementation support for various applications.</li>
                          
                           <li>Demonstrated 50% reduction in manual efforts by engineering a tool that transfers daily 
                                processed files on different servers and
                                populate the results in different databases using Java, IBM MQ and MySQL.</li>  
                            <li>Hands on experience in every phase of software development life cycle (SDLC) using 
                                agile methodologies.</li>
                            <li>Mentored and guided 5 associates ensuring all were trained in system knowledge and 
                                capable of performing assigned duties.</li>
					</ul>

                       </div>
                    </div>
                  </article>
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                   </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}
